import { IcommerceUser, Icompany, IecommerceGuest, Istaff } from '@biz-nest/core-client';
import { lastValueFrom } from 'rxjs';
import { CommerceClient } from '../commerce-client';
import { Company } from '../defines/company/company.define';
import { Staff } from '../defines/user-related/staff.define';

/**
 * Retrieves the list of currency codes that the current user is permitted to use.
 * @returns {Promise<string[]>} A promise that resolves to an array of ISO 4217 currency codes.
 * @throws {Error} If the request fails or the user is not authenticated.
 * @example
 * const currencies = await getCurrencyList();
 * // Returns: ['USD', 'EUR', 'GBP']
 * @since 1.0.0
 */
export const getCurrencyList = (): Promise<string[]> => {
  const observer$ = CommerceClient.ehttp.makeGet<string[]>('/misc/permittable-currency-list');

  return lastValueFrom(observer$);
};

/**
 * Retrieves the current authenticated user's profile information including staff and company details.
 * @returns {Promise<{ staff?: Staff; company: Company }>} A promise that resolves to an object containing:
 * - staff: The staff member's details (if available)
 * - company: The company details
 * @throws {Error} If the request fails or the user is not authenticated.
 * @example
 * const { staff, company } = await myCurrentProfile();
 * console.log(`Welcome, ${staff?.name} from ${company.name}`);
 * @since 1.0.0
 */
export const myCurrentProfile = async(): Promise<{ staff?: Staff; company: Company }> => {
  const observer$ = CommerceClient.ehttp
    .makePost<{ staff?: Istaff; company: Icompany }>('/user-profile/my-current-profile', {});
  const res = await lastValueFrom(observer$);

  return {
    staff: res.staff ? new Staff(res.staff) : undefined,
    company: new Company(res.company)
  };
};

/**
 * Switches the current user's active company profile.
 * @param {string} companyId - The unique identifier of the company to switch to.
 * @returns {Promise<{ staff?: Staff; company: Company }>} A promise that resolves to an object containing:
 * - staff: The updated staff details (if available)
 * - company: The new active company details
 * @throws {Error} If the request fails, user is not authenticated, or company ID is invalid.
 * @example
 * // Switch to a different company
 * const { company } = await switchProfile('company-123');
 * console.log(`Switched to company: ${company.name}`);
 * @since 1.0.0
 */
export const switchProfile = async(companyId: string): Promise<{ staff?: Staff; company: Company }> => {
  const observer$ = CommerceClient.ehttp
    .makePost<{ staff: Istaff; company: Icompany }>('/user-profile/switch-profile', { companyId });
  const res = await lastValueFrom(observer$);

  return {
    staff: res.staff ? new Staff(res.staff) : undefined,
    company: new Company(res.company)
  };
};

/**
 * Retrieves the user's search history matching the given term.
 * @param {string} term - The search term to filter history by.
 * @returns {Promise<string[]>} A promise that resolves to an array of search history items.
 * @throws {Error} If the request fails or the user is not authenticated.
 * @example
 * const history = await getSearchHistory('product');
 * // Returns: ['product 1', 'product catalog']
 * @since 1.0.0
 */
export const getSearchHistory = (term: string): Promise<string[]> => {
  const observer$ = CommerceClient.ehttp
    .makeGet<string[]>(`/user-profile/get-search-history/${encodeURIComponent(term)}`);

  return lastValueFrom(observer$);
};

