/**
 * Retrieves the guest user information.
 * @returns {Promise<EcommerceGuest>} A promise that resolves to the guest user information.
 * @throws {Error} If the request fails or the user is not authenticated.
 * @example
 * const guestInfo = await getGuestInfo();
 * // Returns: { name: 'Guest User', email: 'guest@example.com' }
 * @since 1.0.0
 */
export const getGuestInfo = (): Promise<IecommerceGuest<string, IcommerceUser>> => {
  const observer$ = CommerceClient.ehttp.makeGet<IecommerceGuest<string, IcommerceUser>>('/guest-session/info');

  return lastValueFrom(observer$);
};

