/**
 * Checks if the guest user is linked to an authenticated user.
 * @returns {Promise<{ isLinked: boolean }>} A promise that resolves to an object containing:
 * - isLinked: A boolean indicating whether the guest user is linked to an authenticated user
 * @throws {Error} If the request fails or the user is not authenticated.
 * @example
 * const { isLinked } = await isGuestLinkedToAuthUser();
 * console.log(`Guest user is linked to auth user: ${isLinked}`);
 * @since 1.0.0
 */
export const isGuestLinkedToAuthUser = (): Promise<{ isLinked: boolean }> => {
  const observer$ = CommerceClient.ehttp.makePost<{ isLinked: boolean }>(
    '/guest-session/is-linked-to-auth-user',
    {}
  );

  return lastValueFrom(observer$);
};

